@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login-container {
    background: linear-gradient(128.95deg, #4D212C 51.1%, #A64961 94.27%);
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 100vh;
}

.login-hovering-image-container {
    width: 100%;
    height: 100%;
    max-width: 50rem;
    max-height: 50rem;
    background: linear-gradient(135.27deg, #7D3C4D 0.97%, #6E2639 98.57%);
    border-radius: 25px;

    margin-top: auto;
    margin-bottom: auto;
}

.login-form-container {
    display: flex;
    flex-direction: column;
    justify-content: start;
    height: 100%;
}

.login-input {
    border: none;
    background: transparent;
    box-sizing: border-box;
    width: 80%;

    
    font-family: Montserrat;
    font-style: normal;
    font-size: 3rem;

    color: #FFFFFF;
    border-bottom: solid 0 #fff;  
    transition: all 250ms ease-in-out;
    /* translogin-form-origin: 100% 50%; */
}

.login-input:focus {
    background:transparent;
    outline: none;
    border-bottom: solid 3px attr(content);
}

.login-input-row {
    margin-top: 5rem;
    display: flex;
    flex-direction: row;
    border: 5px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 15px;
    background: rgba(81, 81, 81, 0.4);

    
    margin-left: 5rem;
    margin-right: 5rem;
    transition: all 250ms ease-in-out;
}

.invalid {
    display: inline-block;
    -webkit-animation: wiggle 0.75s linear;
            animation: wiggle 0.75s linear;
    position: relative;
}

.invalid::after {
    content: attr(content);
    position: absolute;
    left: 0;
    bottom: -2rem;
    margin-left: 1rem;

    width:100%;
    
    font-family: Montserrat;
    font-style: bold;
    font-size: 1rem;

    color: #FFFFFF;
}


@-webkit-keyframes wiggle {
    0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
   20%, 60% { -webkit-transform: rotate(2.5deg); transform: rotate(2.5deg); }
   40%, 80% { -webkit-transform: rotate(-2.5deg); transform: rotate(-2.5deg); }
  }


@keyframes wiggle {
    0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
   20%, 60% { -webkit-transform: rotate(2.5deg); transform: rotate(2.5deg); }
   40%, 80% { -webkit-transform: rotate(-2.5deg); transform: rotate(-2.5deg); }
  }

.icon path {
    transition: all 250ms ease-in-out;
}

.login-form-button {
    background: rgba(81, 81, 81, 0.4);
    font-family: Montserrat;
    font-style: normal;
    font-size: 2rem;
    color: #ffffff;
    
    border: 5px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 15px;
    padding: 2rem;
    transition: all 250ms ease-in-out;
    min-width: 15rem;
    text-decoration: none;
    text-align: center;
}

.login-form-button:hover {
    background: #4c212c;
}

.login-form-button-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: auto;
    margin-bottom: 8rem;
}

.login-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 10rem;
}

.login-details-text {
    font-family: Montserrat;
    font-style: normal;
    font-size: 3rem;
    text-align: start;
    
    color: #FFFFFF;
    width: 30rem;
}

.login-details-logo {
    width: 20rem;
}

.login-details-divider {
    height: 5px;
    color: #fff;
    width: 100%;
    background: #fff;
    border: solid #fff 1px;
    opacity: 0.5;
    margin-bottom: 4rem;
    border-radius: 5px;
    display: inline;
    display: initial;
}

svg.icon {
    width:5vw;
    height:5vw;
}

